const StyledLink = styled.a`
  text-decoration: none;
  font-size: 16px;
  padding-left: 15px;
  padding-right: 15px;
  align-items: center;
  display: flex;
  flex-direction: row;
  color: ${Theme.colors.black};
  /* Setting position value other than 'static' makes this element the containing box of its ::after element*/
  position: relative; 

  ::after {
    content: "";
    border-bottom: 3px solid #333333;
    position: absolute;
    bottom: 0px;
    left: 15px;
    transform: scaleX(0);
    transition: transform .2s ease;
    /* The width of ::after is set to the width of its containing box minus the padding values of its containg box*/
    width: calc(100% - 30px); 
    visibility: hidden;
  }

  :hover {
    ::after {
      transform: scaleX(1);
      visibility: visible;
    }
  }

`

const HeaderLink = ({name, text, to}) => {
  return (<StyledLink href={to}>{text}</StyledLink>)
}

const StyledNav = styled.nav`
  display: flex;
  flex: 1;
  flex-direction: row;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  @media (max-width: ${Theme.mediaSize.medium.max}px) {
    mask-image: linear-gradient(to right, transparent, white 20px, white 90%, transparent);
  }
  
`

const Navigation = ({links}) => (
  <StyledNav>
    { links.map(({name, text, to, id}) => {
        return(<HeaderLink 
          key={id}
          to={to}
          text={text} 
          name={name}/>)
    })}
  </StyledNav>
)

const HeaderWrapper = styled(Wrapper)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  transition: all .2s ease;

  /* Hide horizontal scrollbar */

  @media (max-width: ${Theme.mediaSize.medium.max}px) and 
    (min-width: ${Theme.mediaSize.medium.min}px){
        height: 90px;
        
  } 
  @media (max-width: ${Theme.mediaSize.small.max}px) {
        height: 80px;
  } 
`

const NavigationContainer = styled.div`
  display: flex;
  @media (max-width: ${Theme.mediaSize.small.max}px) {
    display: none;
  }
`

const StyledHamburger = styled(Hamburger)`
  display: none;
  @media (max-width: ${Theme.mediaSize.small.max}px) {
    display: block;
    z-index: 100;
  }    
`

const MobileNavigationNav = styled.nav`
  visibility: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: ${Theme.colors.bgDarker};
  transition: all .3s ease;
  z-index: 90;
  opacity: 0;
  transform: translateY(-20%);
  overflow: hidden;
  color: ${Theme.colors.white};
  ${({isOpen}) => 
    isOpen && css` visibility: visible; opacity:1; transform: translateY(0%);`}
  @media (min-width: ${Theme.mediaSize.medium.min}px) {
    display: none;
  }  
`

const MobileStyledLink = styled.a`
  text-decoration: none;
  font-size: 35px;
  padding-top: 15px;
  padding-bottom: 15px;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  color: ${Theme.colors.white};
  /* Setting position value other than 'static' makes this element the containing box of its ::after element*/
  position: relative; 
  opacity: 1;
  transition: opacity .2s ease;
  :hover {
    opacity: 0.7;
  }
`

