const MobileNavWrapper = styled(Wrapper)`
  padding-top: 100px;
  display: flex;
  flex-direction: column;
`

const MobileNavigation = ({isOpen, children}) => (
  <MobileNavigationNav isOpen={isOpen}>
    <MobileNavWrapper>
      {children}
    </MobileNavWrapper>
  </MobileNavigationNav>
)

const MobileLanguageLink = styled.a`
  margin-right: 15px;
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  color: ${Theme.colors.white};
  transition: opacity .2s ease;
  :hover {
    opacity: 0.7;
  }
`

const StyledDropDownButton = styled(DropDownButton)`
  padding-left: 10px;
`
class Header extends React.Component {
  constructor (props, context) {
    super(props, context)
    this.state = {isScrolling: false,
      isMobileNavOpen: false}
    this.handleWindowYScroll = this.handleWindowYScroll.bind(this)
    this.toggleMobileNav = this.toggleMobileNav.bind(this)
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleWindowYScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleWindowYScroll);
  }

  handleWindowYScroll(event) {
    this.setState({isScrolling: (window.pageYOffset > 0)})
  }

  toggleMobileNav(event) {
    this.setState({isMobileNavOpen: !this.state.isMobileNavOpen})
    console.log(this.state)
  }



  render() {
    // const selectedLanguage = languageList.find((lang, index) => {
    //   return (lang.code == language)
    // })
    const {headerLinks, language, slug} = this.props;
    let selectedLanguage = {}
    languageList.forEach((lang, index) => {
      lang.link = slug.replace(language, lang.code)
      if (lang.code === language) {
        selectedLanguage = lang
      }
    })

    let mobileHeaderLinks = headerLinks.map(({name, text, to, id}) => (
      <MobileStyledLink 
        key={`mobile_link_${id}`}
        href={to}
        onClick={this.toggleMobileNav}>
        {text}
      </MobileStyledLink>))

    let mobileLanguageList = languageList.map(({id, text, icon, link}) => (
      <MobileLanguageLink
        key={`mobile_language_${id}`}
        href={link}>
        {icon && <img css={`height: 20px;width: 20px;`} src={icon}></img>}
        <span>{text}</span>
      </MobileLanguageLink>
    ))

    return (  
      <StyledHeader isScrolling={this.state.isScrolling}>
        <HeaderWrapper>
          <Logo css={`z-index: 100;`} fontColor={this.state.isMobileNavOpen ? Theme.colors.white : Theme.colors.black} link={'/' + slug}/>
          <NavigationContainer>
            <Navigation links={headerLinks}></Navigation>
            <StyledDropDownButton list={languageList} selected={selectedLanguage}>
              {/* <LanguagesIcon /> */}
            </StyledDropDownButton>
          </NavigationContainer>
          <StyledHamburger isOpen={this.state.isMobileNavOpen} onClick={this.toggleMobileNav}></StyledHamburger>
          <MobileNavigation isOpen={this.state.isMobileNavOpen}>
            {mobileHeaderLinks}
            <div css={`display: flex; justify-content: center; padding-top: 20px;`}>{mobileLanguageList}</div>
          </MobileNavigation>
        </HeaderWrapper>
      </StyledHeader>)
  }

}

export default Header

