import React from 'react'
import styled, {css} from 'styled-components'
import IndexSection from './IndexSection'
import * as Theme from '../../theme'
import {Images, ImageGallery} from '../../utils/images'

const ContentContainer = styled.div`
    color: ${Theme.colors.fontDark};
`
const ProfileContainer = styled.div`
    display: flex;
    justify-content: flex-start;
    padding-bottom: 50px;

    @media (max-width: ${Theme.mediaSize.small.max}px) {
        flex-wrap: wrap;
        /* flex-direction: column; */
        align-items: center;
        flex-direction: column;
    } 
`

const TextContainter = styled.div`
    max-width: 600px;
    margin-left: 100px;
    > h3 {
        margin: 0;
        padding: 10px 0px 20px 0px;
        font-size: 24px;
    }
    > blockquote {
        margin: 0;
        padding: 0;
        font-size: 16px;
    }
    > p {
        margin: 20px 0px 0px 0px;
        padding: 0;
        font-size: 18px;
        line-height: 1.7;
        text-align: left;
    }

    @media (max-width: ${Theme.mediaSize.small.max}px) {
        margin-left: 0px;
        margin-top: 30px;
        text-align: center;
    } 
`

const ProfileImage = styled.img`
    width: 300px; height: 300px; 
`

const Profile = ({content, className}) => (
    <ProfileContainer className={className}>
        <ProfileImage src={ImageGallery[content.frontmatter.image]} alt=""></ProfileImage>
        <TextContainter dangerouslySetInnerHTML={{__html: content.html}}></TextContainter>
    </ProfileContainer>
)


const IndexSectionTeam = ({metadata, contentNodes}) => {
        return (  
        <IndexSection 
            metadata={metadata}
            color={Theme.colors.fontDark} 
            bgColor={Theme.colors.sectionBackgroundLightGrey}>
            <ContentContainer>
                {
                    contentNodes.sort((a, b) => a.frontmatter.order - b.frontmatter.order)
                        .map((node) => (
                            <Profile key={`index_team_${node.frontmatter.order}`} content={node}></Profile>
                    ))
                }
            </ContentContainer>
        </IndexSection>)
}

export default IndexSectionTeam