import React from "react"
import {Link} from 'gatsby'
import styled from "styled-components"
import logoSvg from '../../images/datu-logo.svg'

const LogoContainer = styled(Link)`
  display: flex;
  /* margin-right: 10px; */
  height: 100%;
  align-items: center;
  text-decoration: none;
`

const Img = styled.img`
  height: 60px;
  width: 60px;
`
const LogoText = styled.span`
  font-size: 28px;
  margin-left: 10px;
  line-height: 20px;
  font-weight: bolder;
  color: ${({fontColor}) => fontColor};
`

const Logo = ({fontColor, link, className}) =>(
  <LogoContainer to={link ? link : "/"} className={className}>
    <Img src={logoSvg}></Img>
    <LogoText fontColor={fontColor}>DATU.TECH</LogoText>
  </LogoContainer>
)

export default Logo