import React from 'react'
import styled, {css} from 'styled-components'
import * as Theme from '../../theme'

const Button = styled.button`
    background: transparent;
    border: 0px none;
    width: 22px;
    height: 16px;
    position: relative;
    padding: 0px;
    outline: currentcolor none medium;
    text-decoration: none;
`

const Span = styled.span`
    display: block;
    background-color: ${Theme.colors.black};
    width: 100%;
    height: 2px;
    margin-bottom: 5px;
    transform-origin: 4px 0px 0px;
    opacity: 1;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1) 0s, background-color 0.5s cubic-bezier(0.77, 0.2, 0.05, 1) 0s, opacity 0.55s ease 0s;    
`

const FirstSpan = styled(Span)`
    ${({isOpen}) => 
        isOpen && css`transform: rotate(45deg) translate(0px, 1px); background-color: ${Theme.colors.white};`}
`

const SecondSpan = styled(Span)`
    ${({isOpen}) => 
        isOpen && css`opacity: 0; transform: rotate(0deg) scale(0.2, 0.2); background-color: ${Theme.colors.white};`}
`

const ThirdSpan = styled(Span)`
    ${({isOpen}) => 
            isOpen && css`transform: rotate(-45deg) translate(1px, -4px); background-color: ${Theme.colors.white};`}
`

const Hamburger = ({isOpen, onClick, className}) => (
    <Button onClick={onClick} className={className}>
        <FirstSpan isOpen={isOpen}></FirstSpan>
        <SecondSpan isOpen={isOpen} ></SecondSpan>
        <ThirdSpan isOpen={isOpen}></ThirdSpan>
    </Button>
)

export default Hamburger