const FooterLogo = styled(Logo)`
   @media (max-width: ${Theme.mediaSize.medium.max}px) {
       margin-bottom: 50px;
   }
`

const StyledFooter = styled.footer`
    background-color: ${Theme.colors.bgDarker};
    color: ${Theme.colors.white};
    padding: 50px 0px;
`

const LinkGroupContainer = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    flex-grow: 1;
    @media (max-width: ${Theme.mediaSize.medium.max}px) {
        justify-content: flex-start;
    }
    /* TODO: resposive design */
`

/**
 * LinkGroup
 */
const LinkGroupLink = styled.a`
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    :hover {
        opacity: 0.8
    }
`

const LinkGroupDiv = styled.div`
    /* display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 50%;
    padding-top: 40px;
    padding-left: 30px; */
    font-size: 14px;
    @media (max-width: ${Theme.mediaSize.medium.max}px) {
      margin-bottom: 50px;
  }
`

const LinkGroup = ({title, children}) => (
    <LinkGroupDiv>
        <h3 css={`font-size: 20px; margin-bottom: 25px;`}>{title}</h3>
        {children}
    </LinkGroupDiv>
)


/**
 * SitiInfoDiv
 */
const SiteInfoContainer = styled.div`
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: flex-start; 
    padding-top: 50px;
    flex-wrap: wrap;
    line-height: 20px;
    @media (max-width: ${Theme.mediaSize.medium.max}px) {
        padding-top: 0;
        justify-content: flex-start;
    }
`

const SiteInfoLink = styled(LinkGroupLink)`
    margin-right: 25px;
`

const SiteInfoDiv = ({siteinfo}) => (
    <SiteInfoContainer>
        <span css={`margin-right: 25px;`}>{`© ${new Date().getFullYear()} ${siteinfo.copyright}`}</span>
        <SiteInfoLink href="javascript:void(0)">{siteinfo.privacy_policy}</SiteInfoLink>
        <SiteInfoLink href="###">{siteinfo.terms_of_use}</SiteInfoLink>
    </SiteInfoContainer>
)

const SocialIconLink = ({item}) => (
    <LinkGroupLink css={`padding-right: 20px;`}>
        <img css={`width: 40px;}`} src={item.icon} to={item.link} alt={item.name}></img>
    </LinkGroupLink>
)

const FooterContentDiv = styled.div`   
    display: flex; 
    justify-content: flex-start;
    flex-wrap: wrap;
`

const Footer = ({content, slug}) => {
    const {FooterLinkGroups, SiteInfo} = content.edges[0].node
    return (
        <StyledFooter>
            <Wrapper>
                <FooterContentDiv>
                    <FooterLogo fontColor={Theme.colors.white} link={'/' + slug}></FooterLogo>
                    <LinkGroupContainer>
                        <LinkGroup title={FooterLinkGroups.contacts.title}>
                            <LinkGroupLink href="mailto:info@datu.tech">info@datu.tech</LinkGroupLink>
                        </LinkGroup>
                        <LinkGroup title={FooterLinkGroups.social.title}>
                            {
                                SocialIconList.map((item, index) => (
                                    <SocialIconLink key={`social_icon_${index}`} item={item}></SocialIconLink>
                                ))
                            }
                        </LinkGroup>                    
                    </LinkGroupContainer>
                </FooterContentDiv>
                <SiteInfoDiv siteinfo={SiteInfo}></SiteInfoDiv>
            </Wrapper>
        </StyledFooter>
    )
}

export default Footer