const CoverLogo = styled.img`
  width: 20%; 
  height: auto;
  padding-bottom: 50px;

  @media (max-width: ${Theme.mediaSize.small.max}px) {
    width: 40%;
  } 
`

const Cover = ({contentNodes}) => {
  let edge = (contentNodes && contentNodes.length > 0) ? contentNodes[0] : null
  const {title, slogan} = edge.node.frontmatter
  return (
    <CoverContainer>
    <Wrapper css={`display: flex; flex-direction: column; align-items: center;`}>
      <CoverLogo src={CoverLogoSvg}></CoverLogo>
      <CoverTitle>{title}</CoverTitle>
      <CoverSlogan>{slogan}</CoverSlogan>
    </Wrapper>
  </CoverContainer>
  )
}

var smoothscroll

class Index extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      // sectionMap: {}
    }
    this.sectionMap = {}
    this.scrollStopEvent = null
    this.handleWindowHashChange = this.handleWindowHashChange.bind(this)
    this.registerSection = this.registerSection.bind(this)
    this.unregisterSection = this.unregisterSection.bind(this)
    this.handleWindowYScroll = this.handleWindowYScroll.bind(this)
  }

  componentDidMount() {
    smoothscroll = require("smoothscroll-polyfill")
    smoothscroll.polyfill()
    window.addEventListener('scroll', this.handleWindowYScroll);
    window.addEventListener('hashchange', this.handleWindowHashChange);
  }

  componentWillUnmount() {
    window.addEventListener('scroll', this.handleWindowYScroll);
    window.removeEventListener('hashchange', this.handleWindowHashChange);
  }

  handleWindowYScroll(event) {
    if (this.scrollStopEvent) {
      clearTimeout(this.scrollStopEvent)
    }

    let self = this
    this.scrollStopEvent = setTimeout(() => {
      // Scroll stopped
      let currentOffset = window.pageYOffset
      let currentHash = window.location.hash
      if (currentHash && currentHash.length > 0) {
        let node = self.sectionMap[currentHash.substring(1)]
        if (node && node.current &&
          currentOffset >= node.current.offsetTop &&
          currentOffset < node.current.offsetTop + node.current.offsetHeight) {
          // No need to change
          return
        }
      }
      
      let hash = ' '
      for (let [key, node] of Object.entries(self.sectionMap)) {
        if (currentOffset >= node.current.offsetTop && 
          currentOffset < node.current.offsetTop + node.current.offsetHeight) {
            hash = '#' + key
            break;
          }
      }
      if (hash !== currentHash) {
        // replaceState() function changed the hash in url without 1. triggering the hashchange event 2. page reloading / refreshing 
        window.history.replaceState(null, null, hash);
        console.log(`hash auto updated. old: ${currentHash}, new: ${hash}`)
      }
    }, 500)
  }

  handleWindowHashChange(event) {
    console.log(`hash changed. hash: ${window.location.hash}`)
    let hash = window.location.hash.substring(1)
    if (hash === 'contact') {
      var scrollingElement = (document.scrollingElement || document.body);
      window.scrollTo({left: 0, top: scrollingElement.scrollHeight, behavior: 'smooth'}) 
    }
    else {
      let toSectionRef = this.sectionMap[hash]
      if (toSectionRef && toSectionRef.current && toSectionRef.current.offsetTop) {
        window.scrollTo({left: 0, top: toSectionRef.current.offsetTop, behavior: 'smooth'}) 
      }
    }



  }

  registerSection(id, ref) {
    this.sectionMap[id] = ref
    console.log(`section registed id: ${id}, ref: ${ref.toString()}`)
  }

  unregisterSection(id) {
    delete this.sectionMap[id]
    console.log(`section unregisted. id: ${id}`)
  }

  render() {
    {
      const {data, pageContext} = this.props
      const {pageComponents, 
        pageContent, 
        pageSections, 
        layoutHeaderContent, 
        layoutFooterContent} = data
      let language = pageContext.dir.match(LANGUAGE_CODE_REGEX)[0]
      let sectionContents = {}
      data.pageComponents.edges.forEach((edge, index) => {
        const {section} = edge.node.frontmatter
        if(!Utility.objectHasKey(sectionContents, section)) {
          sectionContents[section] = []
        }
        sectionContents[section].push(edge.node)
      })

      return (
      <Layout headerContent={layoutHeaderContent} footerContent={layoutFooterContent} language={language} slug={pageContext.slug}>
        <TitleAndMetaTags title={data.layoutSiteMetadata.edges[0].node.SiteMetadata.SiteTitle} />
        <IndexContentDiv>
          <SectionContext.Provider value={{registerSection: this.registerSection, unregisterSection: this.unregisterSection}}>
            <Cover contentNodes={pageContent.edges}></Cover>
            <IndexSectionAbout 
              metadata={pageSections.sections.about} 
              contentNodes={sectionContents.about}/>
            <IndexSectionTechnologies metadata={pageSections.sections.technologies} contentNodes={sectionContents.technologies}></IndexSectionTechnologies>
            <IndexSectionProducts metadata={pageSections.sections.products} contentNodes={sectionContents.products}></IndexSectionProducts>
            {/* <IndexSectionNews metadata={pageSections.sections.news} contentNodes={sectionContents.news}></IndexSectionNews> */}
            <IndexSectionTeam  metadata={pageSections.sections.team} contentNodes={sectionContents.team}></IndexSectionTeam>            
          </SectionContext.Provider>
        </IndexContentDiv>
      </Layout>)
    }
  }
}

export const pageQuery = graphql`
  query IndexMarkdown($slug: String!, $dir: String!) {
    pageComponents: allMarkdownRemark(
      filter: {fields: {slug: {eq: $slug}}, frontmatter: {type: {eq: "component"}}}
    ) {
      edges {
        node {
          html,
          excerpt(pruneLength: 500),
          frontmatter {
            type,
            title,
            section,
            order,
            image
          },
          fields {
            slug,
            dir
          }
        }
      }
    }
    pageContent: allMarkdownRemark(
      filter: {fields: {slug: {eq: $slug}}, frontmatter: {type: {eq: "page"}}}
    ) {
      edges {
        node {
          html
          excerpt(pruneLength: 500)
          frontmatter {
            title,
            slogan
          }
          fields {
            path,
            slug
          }
        }
      }
    }
    pageSections: indexSectionsYaml(fields: {dir: {eq: $dir}}){
      sections {
        about {
          title,
          id
        },
        technologies {
          title,
          id
        },
        products {
          title,
          id
        },
        news {
          title,
          id
        },
        team {
          title,
          id
        }
      }
    }
    layoutSiteMetadata: allLayoutYaml (
      filter: {fields: {dir: {eq: $dir}}, group: {eq: "SiteMetadata"}}
    ) {
      edges{
        node{
          SiteMetadata {
            SiteTitle
          }
        }
      }
    }
    layoutHeaderContent: allLayoutYaml (
      filter: {fields: {dir: {eq: $dir}}, group: {eq: "HeaderLinks"}}
    ){
        edges{
            node{
                id,
                name,
                text,
                to,
                group,
            }
        }
    }
    layoutFooterContent: allLayoutYaml (
      filter: {fields: {dir: {eq: $dir}}, group: {eq: "FooterLinkGroups"}}
    ) {
      edges{
        node{
            group,
            FooterLinkGroups{
              contacts {
                title
              },
              social {
                title
              }
            },
            SiteInfo {
              copyright,
              terms_of_use,
              privacy_policy
            }
        }
      }        
    }

  }
`;

export default Index