import React from 'react'
import styled, {css} from 'styled-components'
import ChevronSvg from './ChevronSvg'
import * as Theme from '../../theme'

const Container = styled.div`
    font-size: 14px;
`
const StyledButton = styled.button`
    height: 35px;
    padding-top: 5px;
    padding-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    align-items: center;
    border: 1px solid rgb(218, 217, 217);
    border-radius: 2px;
    transition: all 0.13s linear 0s;
    text-align: center;
    vertical-align: middle;
    font-size: 14px;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    width: 150px;

    &:hover {
        border: 1px solid ${Theme.colors.black};
    }
`

const MenuContainer = styled.div`
    position: absolute;
    box-shadow: 0 2px 8px rgba(0,0,0,0.15);
    width: 150px;
    display: none;
    max-height: 0%;
    opacity: 0;
    overflow: hidden;
    background-color: ${Theme.colors.white};
    transition: all 0.2s ease;
    
    display: block;
    z-index: -100;
    ${({isOpen}) => isOpen && css`
        max-height: 100%;
        opacity: 1;
        z-index: 100;
    `}
    
`

const DropDownUl = styled.ul`
    list-style: none;
    padding: 0px;
`
const DropDownLi = styled.li`
    clear: both;
    margin-bottom: 5px;
    padding: 5px 16px;
    color: rgba(0,0,0,0.65);
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    white-space: nowrap;
    cursor: pointer;
    transition: all .3s;

    &:hover {
        background-color: ${Theme.colors.lightOrige}; 
    }
`

const ItemIcon = styled.img`
    height: 20px;
    width: 20px;
    margin-right: 8px;
    @media (max-width: ${Theme.mediaSize.medium.max}px) {
        margin-right: 0px;
    }
`


const ItemLink = styled.a`
    text-decoration: none;
    display: flex;
    align-items: center;
    &:visited {
        color: inherit;
    }
`

const ButtonText = styled.span`
    @media (max-width: ${Theme.mediaSize.medium.max}px) {
        display: none;
    }
`

class DropDownButton extends React.Component {
    constructor (props, context) {
        super(props, context)
        this.state = {isOpen: false}
        this.setContainerRef = this.setContainerRef.bind(this)
        this.handleClickOutside = this.handleClickOutside.bind(this)
        this.toggleOpen = this.toggleOpen.bind(this)
    }

    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside)
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside)
    }

    handleClickOutside(event) {
        if (this.containerRef && !this.containerRef.contains(event.target)) {
            this.setState({isOpen: false})
        }
    }

    setContainerRef(node) {
        this.containerRef = node;
    }

    toggleOpen() {
        this.setState({isOpen: !this.state.isOpen})
    }

    render() {
        const {list, selected, children} = this.props
        const others = list.reduce((accumulator, currentValue, currentIndex) => {
            if (currentValue.id !== selected.id){
                accumulator.push(currentValue)
            }
            return accumulator
        },
        [])
        return (
        <Container ref={this.setContainerRef}>
            {children}
            <StyledButton onClick={this.toggleOpen}>
                {selected.icon && <ItemIcon src={selected.icon}></ItemIcon>}
                <ButtonText>{selected.text}</ButtonText>
                <ChevronSvg isOpen={this.state.isOpen}></ChevronSvg>
            </StyledButton>
            <MenuContainer isOpen={this.state.isOpen}>
            <DropDownUl>
                {others.map(({id, text, icon, link}) => {
                    return (
                    <DropDownLi key={id}>
                        <ItemLink href={link}>
                            {icon && <ItemIcon src={icon}></ItemIcon>}
                            <span>{text}</span>
                        </ItemLink>
                    </DropDownLi>)
                })}                  
            </DropDownUl>
              
            </MenuContainer>
        </Container>)
    }
}
export default DropDownButton