import React from 'react'
import styled from 'styled-components'
import IndexSection from './IndexSection'
import * as Theme from '../../theme'
import {Images} from '../../utils/images'

const ContentContainer = styled.div`
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-bottom: 60px;
`

const DescriptionDiv = styled.div`
    max-width: 485px;
    flex-basis: 40%;
    @media (max-width: ${Theme.mediaSize.medium.max}px) and 
    (min-width: ${Theme.mediaSize.medium.min}px){
        flex-basis: 40%;
        flex-shrink: 1;
    } 

    @media (max-width: ${Theme.mediaSize.small.max}px) {
        margin-top: 40px;
        flex-basis: 100%;
        order: 2;
    } 
`

const TextContainer = styled.div`

    > h3 {
        margin: 0;
        padding: 0px 0px 0px 0px;
        font-size: 30px;
    }
    > p {
        font-size: 16px;
        margin: 0;
        padding: 0;
    }
    > ul {
        margin-top: 30px;
        list-style-type: circle;
        list-style-position: inside;
        line-height: 1.7;
    }
    > ul li {

    }
`

const Button = styled.button`
    margin-top: 50px;
    width: 140px;
    height: 40px;
    background-color: ${Theme.colors.brand};
    color: ${Theme.colors.white};
    text-align: center;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: all .3s ease;
    outline: none;

    :hover {
        background-color: ${Theme.colors.white};
        color: ${Theme.colors.fontDark};
    }
`

const Image = styled.img`
    max-width: 100%;
    height: auto;
`

const ImageContainer = styled.div`
    max-width: 400px;
    flex-basis: 50%;
    @media (max-width: ${Theme.mediaSize.medium.max}px) and 
    (min-width: ${Theme.mediaSize.medium.min}px){
        flex-basis: 50%;
        flex-shrink: 1;
    } 

    @media (max-width: ${Theme.mediaSize.small.max}px) {
        flex-basis: 100%;
        order: 1;
    } 
`

const IndexSectionProducts = ({metadata, contentNodes}) => {
    return (  
        <IndexSection 
            metadata={metadata}
            color={Theme.colors.white} 
            bgColor={Theme.colors.sectionBackgroundDark}>
            <ContentContainer>
                <DescriptionDiv>
                    <TextContainer dangerouslySetInnerHTML={contentNodes && {__html: contentNodes[0].html}}></TextContainer>
                    <Button>Learn More</Button>
                </DescriptionDiv>
                <ImageContainer>
                    <Image src={Images.index.products.dtfunds} alt="DTFunds"></Image>
                </ImageContainer>
            </ContentContainer>
        </IndexSection>)
}

export default IndexSectionProducts