import languages from '../../content/languages.yml'
import rounded_en_us_svg from '../icons/rounded_en_us.svg'
import rounded_zh_cn_svg from '../icons/rounded_zh_cn.svg'
import rounded_zh_hk_svg from '../icons/rounded_zh_hk.svg'

function getLanguageIcon(code) {
    switch(code) {
        case "en_us":
        return rounded_en_us_svg
        case "zh_cn":
        return rounded_zh_cn_svg
        case "zh_hk":
        return rounded_zh_hk_svg
        default:
        return null
    }
}

const languageList = languages.map((item) => ({
    ...item,
    id: item.code,
    text: item.translated_name,
    icon: getLanguageIcon(item.code)
}))

export {
    languageList
}