import React, {Component} from 'react'
import styled from 'styled-components'
import * as Theme from '../../../theme'
import Wrapper from '../../../components/wrapper'
import {SectionContext} from '../..//contexts/section_context'
// import { supportsHistory } from 'history/DOMUtils';


const StyledSection = styled.section`
    background-color: ${({bgColor}) => bgColor || Theme.colors.white};
    color: ${({color}) => color || Theme.colors.black};

`

const SectionTitle = styled.h2`
    margin: 0;
    padding-top: 100px;
    padding-bottom: 40px;
    font-size: 50px;
    font-weight: 300;
`
class IndexSection extends Component {
    constructor(props, context) {
        super(props, context)
        this.sectionRef = React.createRef()
        this.sectionId = ""
    }

    componentDidMount() {
        let {registerSection} = this.context
        if (registerSection) {
            registerSection(this.sectionId, this.sectionRef)
        }
    }

    componentWillUnmount() {
        let {unregisterSection} = this.context
        if (unregisterSection) {
            unregisterSection(this.sectionId)
        }
    }

    render() {
        let {metadata, children, color, bgColor} = this.props
        this.sectionId = metadata.id;
        return (
        <StyledSection color={color} bgColor={bgColor} ref={this.sectionRef}>
            <Wrapper>
                <SectionTitle>{metadata.title}</SectionTitle>
                <div>{children}</div>
            </Wrapper>
        </StyledSection>)        
    }
}

IndexSection.contextType = SectionContext
export default IndexSection