import React, {Component} from 'react'
import styled from 'styled-components'
import IndexSection from './IndexSection'
import {Images} from '../../utils/images'
import * as Theme from '../../theme'

const SectionAboutContainer = styled.div`
  display: flex;
  justify-content: space-between;
  padding-bottom: 50px;

  @media (max-width: ${Theme.mediaSize.small.max}px) {
    flex-wrap: wrap;
  } 
`
const SectionAboutDescript = styled.div`
  flex-basis: 500px;

  > h3 {
    margin: 20px 0px 15px 0px;
    padding: 0;
    font-size: 30px;
  }

  > p {
    font-size: 16px;
    margin: 20px 0px 5px 0px;
    padding: 0;
    line-height: 1.7;
  }

  @media (max-width: ${Theme.mediaSize.medium.max}px) and 
    (min-width: ${Theme.mediaSize.medium.min}px){
        flex-basis: 40%;
        flex-shrink: 1;
  } 

  @media (max-width: ${Theme.mediaSize.small.max}px) {
    flex-basis: 100%;
    order: 2;
    margin-top: 40px;
  } 
`

const SectionAboutImg = styled.img`
  max-width: 100%;
  height: auto;


`

const ImgContainer = styled.div`
  flex-basis: 700px;
  max-width: 700px;
  flex-grow: 1;
  @media (max-width: ${Theme.mediaSize.medium.max}px) and 
    (min-width: ${Theme.mediaSize.medium.min}px){
        flex-basis: 50%;
        flex-shrink: 1;
  } 

  @media (max-width: ${Theme.mediaSize.small.max}px) {
    flex-basis: 100%;
    order: 1;
  } 
`
class IndexSectionAbout extends Component {
    constructor(props, context) {
        super(props, context)
    }

    render() {
        const {metadata, contentNodes} = this.props
        let content = (contentNodes && (contentNodes.length > 0)) ? contentNodes[0] : null
        return (  
            <IndexSection 
                metadata={metadata}
                color={Theme.colors.white} 
                bgColor={Theme.colors.sectionBackgroundDark}>
                <SectionAboutContainer>
                    {content && content.html && 
                    <SectionAboutDescript dangerouslySetInnerHTML={{__html: content.html}}>
                        
                    </SectionAboutDescript>}
                    <ImgContainer>
                      <SectionAboutImg src={Images.index.about}></SectionAboutImg>
                    </ImgContainer>
                    
                </SectionAboutContainer>
                
            </IndexSection>)
    }
}
export default IndexSectionAbout