/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"

import Header from "./header"
import Footer from "./footer"
import styled from "styled-components"
import {ResetStyle} from '../globalStyles'

const LayoutContainer = styled.div`
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 40px);
`

const PageContentDiv = styled.div`
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  flex-grow: 1;
  justify-content: stretch;
  flex-basis: auto;
  align-items: flex-start;
  flex: 1 0 auto;
  margin-top: 100px;
`

const Layout = ({ children, headerContent, footerContent, language, slug}) => (
  <LayoutContainer>
    <ResetStyle />
    <Header 
      headerLinks = {headerContent.edges.reduce((accumulator, currentValue, currentIndex, array) => {
        if (currentValue.node.group === 'HeaderLinks') {
          accumulator.push(currentValue.node)
        }
        return accumulator
      }, 
      [])} 
      language={language}
      slug={slug}
    />
    <PageContentDiv>
      {children}
    </PageContentDiv>
    <Footer content={footerContent} slug={slug}/>
  </LayoutContainer>
)

export default Layout
