import React from 'react'
import styled from 'styled-components'
import IndexSection from './IndexSection'
import * as Theme from '../../theme'
import blockchainSvg from '../../images/tech_blockchain.svg'
import dappSvg from '../../images/tech_dapp.svg'
import securitySvg from '../../images/tech_security.svg'

const images = {
    Blockchains: blockchainSvg,
    DApps: dappSvg,
    Security: securitySvg
}

const ContentContainer = styled.div`
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
`

const TextContainer = styled.div`
    width: 350px;
    text-align: center;
    padding-bottom: 50px;
    > h3 {
        margin: 20px 0px 15px 0px;
        padding: 0;
        font-size: 30px;
    }
    > p {
        font-size: 16px;
        margin: 0;
        padding: 0;
        line-height: 1.7;
    }
`

const ColumnImg = styled.img`
    height: 180px; 
    width: 180px; 
    margin-left: auto; 
    margin-right: auto; 
    display: block;
    margin-bottom: 20px;
    transition: all .2s ease;
    :hover {
        transform: scale(1.1);
        /* box-shadow: 0 2px 13px rgba(0,0,0,.2); */
    }
`

const Column = ({content, className}) => (
    <div className={className}>
        <ColumnImg src={images[content.frontmatter.title]} alt={content.frontmatter.title}></ColumnImg>
        <TextContainer dangerouslySetInnerHTML={{__html: content.html}}></TextContainer>
    </div>
)


const IndexSectionTechnoligies = ({metadata, contentNodes}) => {
    return (  
        <IndexSection 
            metadata={metadata}
            color={Theme.colors.fontDark} 
            bgColor={Theme.colors.white}>
            <ContentContainer>
                {contentNodes.sort((a, b) => (a.frontmatter.order - b.frontmatter.order))
                    .map((node, index) => (
                    <Column key={`index_technologies_${node.frontmatter.order}`} content={node}></Column>
                ))}
            </ContentContainer>
        </IndexSection>)
}

export default IndexSectionTechnoligies